<template>
  <div class="limiter">
		<div class="container-login100">
			<div class="wrap-login100">
				<div class="login100-pic js-tilt" data-tilt>
          <router-link :to="{name : 'Home'}">
					  <img src="../assets/Logo.png" alt="IMG">
          </router-link>
				</div>

				<form class="login100-form validate-form" @submit="signin">
					<span class="login100-form-title">
						Entrar
					</span>

					<div class="wrap-input100 validate-input" data-validate = "Insira um email válido, tipo: exemplo@gmail.com">
						<input class="input100" type="text" name="email" placeholder="E-mail" v-model="email" required>
						<span class="focus-input100"></span>
						<span class="symbol-input100">
							<i class="fa fa-envelope" aria-hidden="true"></i>
						</span>
					</div>

					<div class="wrap-input100 validate-input" data-validate = "A senha é obrigatória">
						<input class="input100" type="password" name="pass" placeholder="Senha" v-model="password" required>
						<span class="focus-input100"></span>
						<span class="symbol-input100">
							<i class="fa fa-lock" aria-hidden="true"></i>
						</span>
					</div>
					
					<div class="container-login100-form-btn">
						<button class="login100-form-btn">
							Entrar
						</button>
					</div>

					<div class="text-center p-t-136">
            <router-link :to="{name: 'Signup'}">
              <a class="txt2" href="#">
                Crie sua conta agora
                <i class="fa fa-long-arrow-right m-l-5" aria-hidden="true"></i>
              </a>
            </router-link>
					</div>
				</form>
			</div>
		</div>
	</div>
</template>

<script>
export default {
  name: 'Signin',
  props : [ "baseURL"],
  data() {
    return {
      email: null,
      password: null,
      loading: null
    }
  },
  methods : {
    async signin(e) {
      e.preventDefault();
      this.loading = true;

      const user = {
        email: this.email,
        password: this.password
      }

      await axios({
        method: 'post',
        url: this.baseURL + "user/signIn",
        data : JSON.stringify(user),
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(res => {
        localStorage.setItem('token', res.data.token);
        this.$emit("refreshNav");
        this.$router.push({name:'Home'});
      })
      .catch(err => {
        swal({
          text: "Falha no login!",
          icon: "error",
          closeOnClickOutside: false,
        });
        console.log(err);
      })
      .finally(() => {
        this.loading = false;
      })
    }
  },
  mounted() {
    this.loading = false;
  }
}
</script>

